<style>
view {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.tabbar-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  /* height: 110rpx; */
  /* box-shadow: 0 0 5px #999; */
  display: flex;
  align-items: center;
  padding: 5rpx 0;
  color: #999999;
